import { Component, Element, Event, EventEmitter, Host, Prop, State, Watch, h } from '@stencil/core';
import { FetchedProperty } from '@/services/property.service';
import { GetACByACID } from './legacy.types';
import axios from 'axios';
import Token from '@/models/Token';

type SwitcherMode = 'dropdown' | 'dialog' | 'read-only';

@Component({
  tag: 'ir-property-switcher',
  styleUrl: 'ir-property-switcher.css',
  scoped: true,
})
export class IrPropertySwitcher {
  @Element() el: HTMLIrPropertySwitcherElement;

  @Prop() mode: 'dropdown' | 'dialog' = 'dialog';
  @Prop() ticket: string;
  @Prop() baseUrl: string;

  @State() open: boolean = false;
  @State() selectedProperty?: FetchedProperty;
  @State() linkedProperties: FetchedProperty[] = [];
  @State() displayMode: SwitcherMode = 'read-only';

  private token = new Token();

  /** Emits whenever the user selects a new property from the switcher dialog. */
  @Event({ bubbles: true, composed: true }) propertyChange: EventEmitter<FetchedProperty>;

  async componentWillLoad() {
    if (this.baseUrl) this.token.setBaseUrl(this.baseUrl);
    if (this.ticket) {
      this.token.setToken(this.ticket);
      await this.initializeLinkedProperties();
    }
  }

  @Watch('ticket')
  handleTicketChange(newValue, oldValue) {
    if (newValue !== oldValue) {
      this.token.setToken(this.ticket);
      this.initializeLinkedProperties();
    }
  }

  private getStoredSelectedAc(): GetACByACID | null {
    const raw = localStorage.getItem('_Selected_Ac');
    if (!raw) {
      return null;
    }
    try {
      return JSON.parse(raw) as GetACByACID;
    } catch (error) {
      console.error('Failed to parse _Selected_Ac from localStorage', error);
      return null;
    }
  }

  private updateSelectedProperty(selectedAc: GetACByACID) {
    this.selectedProperty = {
      A_NAME: selectedAc.My_User?.USERNAME ?? '',
      COUNTRY_CODE: selectedAc.COUNTRY_ID as any,
      COUNTRY_NAME: selectedAc.My_Country?.L1_NAME_REF ?? '',
      PROPERTY_ID: selectedAc.AC_ID,
      PROPERTY_NAME: selectedAc.NAME,
    };
  }

  private async initializeLinkedProperties() {
    const selectedAc = this.getStoredSelectedAc();
    if (!selectedAc) {
      this.linkedProperties = [];
      this.resolveDisplayMode(false);
      return;
    }

    this.updateSelectedProperty(selectedAc);
    await this.fetchLinkedProperties(selectedAc.AC_ID);
    this.resolveDisplayMode(true);
  }

  private async fetchLinkedProperties(acId: number) {
    try {
      const { data } = await axios.post('/Fetch_Linked_Properties', { AC_ID: acId });
      if (data.ExceptionMsg !== '') {
        throw new Error(data.ExceptionMsg);
      }
      this.linkedProperties = Array.isArray(data.My_Result) ? data.My_Result : [];
    } catch (error) {
      console.error('Failed to fetch linked properties', error);
      this.linkedProperties = [];
    }
  }

  private resolveDisplayMode(hasSelectedAc: boolean) {
    const userInfoRaw = localStorage.getItem('UserInfo_b');
    let userInfo = null;
    if (userInfoRaw) {
      try {
        userInfo = JSON.parse(userInfoRaw);
      } catch (error) {
        console.error('Failed to parse UserInfo_b from localStorage', error);
      }
    }
    const userTypeCode = String(userInfo?.USER_TYPE_CODE ?? '');
    if (userTypeCode === '1' || userTypeCode === '4') {
      this.displayMode = 'dialog';
      return;
    }
    if (!hasSelectedAc || !this.linkedProperties.length) {
      this.displayMode = 'read-only';
      return;
    }
    this.displayMode = 'dropdown';
  }

  private trigger() {
    return (
      <ir-custom-button
        onClickHandler={() => {
          this.open = !this.open;
        }}
        withCaret
        variant="neutral"
        appearance="plain"
      >
        <p class="property-switcher__trigger">{this.selectedProperty?.PROPERTY_NAME ?? 'Select property'}</p>
      </ir-custom-button>
    );
  }

  private handlePropertySelected = async (event: CustomEvent<FetchedProperty>) => {
    await this.applySelectedProperty(event.detail);
  };

  private handleDropdownSelect = async (event: CustomEvent<string>) => {
    const selectedId = Number(event.detail);
    const property = this.linkedProperties.find(item => item.PROPERTY_ID === selectedId);
    if (!property) {
      return;
    }
    await this.applySelectedProperty(property);
  };

  private async applySelectedProperty(property: FetchedProperty) {
    this.selectedProperty = property;
    this.open = false;

    try {
      const { data } = await axios.post('/Get_Ac_By_AC_ID_Adv', {
        AC_ID: property.PROPERTY_ID,
        Bypass_Caching: true,
        IS_BACK_OFFICE: true,
      });
      if (data.ExceptionMsg !== '') {
        throw new Error(data.ExceptionMsg);
      }
      localStorage.setItem('_Selected_Ac', JSON.stringify(data.My_Result ?? data));
    } catch (error) {
      console.error('Failed to fetch selected property details', error);
    }

    this.propertyChange.emit(property);
    await this.initializeLinkedProperties();
  }

  private renderReadOnly() {
    return <p class="property-switcher__trigger">{this.selectedProperty?.PROPERTY_NAME ?? 'Property'}</p>;
  }

  render() {
    return (
      <Host>
        {this.displayMode === 'read-only' && this.renderReadOnly()}
        {this.displayMode === 'dropdown' && (
          <ir-select
            showFirstOption={false}
            selectedValue={this.selectedProperty?.PROPERTY_ID?.toString() ?? ''}
            data={this.linkedProperties.map(property => ({
              value: property.PROPERTY_ID?.toString(),
              text: `${property.PROPERTY_NAME} ${property.COUNTRY_NAME}`,
            }))}
            onSelectChange={this.handleDropdownSelect}
          ></ir-select>
        )}
        {this.displayMode === 'dialog' && (
          <div>
            {this.trigger()}
            <ir-dialog
              onIrDialogAfterHide={e => {
                e.stopImmediatePropagation();
                e.stopPropagation();

                this.open = false;
              }}
              withoutHeader
              open={this.open}
              label="Find property"
              class="property-switcher__dialog"
            >
              {this.open && (
                <ir-property-switcher-dialog-content
                  open={this.open}
                  selectedPropertyId={this.selectedProperty?.PROPERTY_ID}
                  properties={this.linkedProperties}
                  onPropertySelected={this.handlePropertySelected}
                ></ir-property-switcher-dialog-content>
              )}
            </ir-dialog>
          </div>
        )}
      </Host>
    );
  }
}
